#full-work {
  background: {
    color: $color-primary; }
  width: 100%;
  height: 100%;
  max-height: 100%;
  overflow-y: auto;
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  padding: {
    top: $height-header + $padding-top-page; }

  @media #{$xsmall-max} {
    padding: {
      top: 20px; } }

  h1 {
    @extend %h1;
    text-align: left;
    margin: {
      bottom: 10px; } }

  p {
    @extend %p; }

  ul.tags {
    width: 100%;
    margin: {
      bottom: 30px; }

    li {
      @include rounded(10px);
      font: {
        size: .6em; }
      display: inline-block;
      margin: 2px;
      padding: 5px 10px;
      color: $color-soft;
      background: {
        color: $color-special; } } }

  ul.photos {
    li {
      height: 150px;
      margin: 10px 0;
      overflow: hidden;

      img {
        width: 100%;

        &:hover {
          width: 150%;
          margin: {
            left: -25%;
            top: -5%; } } } } }

  .button {
    a {
      color: $color-secondary;
      cursor: pointer;
      position: absolute;
      top: 20px;
      right: 20px;
      font: {
        size: .8em; }

      &:hover {
        color: $color-special; }

      i {
        font: {
          size: 1.2em; } } } } }
