#works {
  @extend %page;

  @media #{$medium-max} {
    padding: {
      bottom: 40px; } }

  @media #{$small-max} {
    margin: {
      top: 0; }
    padding: {
      top: 0; } }

  h1 {
    @extend %h1; }

  .box {
    cursor: pointer;
    position: relative;

    .image {
      height: 160px;
      overflow: hidden;

      img {
        width: 100%;
        min-width: 355px; } }

    .details {
      position: absolute;
      text-align: center;
      background: {
        color: $color-special; }
      height: 160px;
      width: 100%;
      top: 0;
      cursor: auto;

      h1 {
        font: {
          size: 1.4em; }
        margin: 25px 0 10px 0; }

      ul.tags {

        li {
          display: inline-block;
          font: {
            size: 0.8em; }
          margin: 0 2px;

          a {
            color: $color-secondary-soft; } } }

      ul.buttons {
        text-align: center;

        li {
          display: inline-block;
          width: 25%;
          margin: 0 auto;
          font: {
            size: .8em; }

          .btn {
            min-width: 100%; } } } } } }
